import React from "react";
import { Table } from "semantic-ui-react";
import ParameterInput from "./ParameterInput";


export default function ParameterInputs({inputs, inputUpdated})
{
    const listItems = inputs.map(({name, unit, value}) =>
        <ParameterInput as={Table.Row} key={name} parameterName={name} parameterUnit={unit} value={value} onChange={(newVal)=>inputUpdated(name, newVal)}/>);

    return (
            <Table compact>
                <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell>Name</Table.HeaderCell>
                        <Table.HeaderCell>Value</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>
                <Table.Body>
                    {listItems}
                </Table.Body>
            </Table>
    )
}