import React from "react";
import Plot from 'react-plotly.js';
import { Panel } from "./horn_model_definitions";


export default function PanelPlot({ panels }: { panels: Map<string, Panel> }) {
  /*def visualize_panels(panels):
    """ Array of Panels """
    for _, panel in panels.items():
        plt.plot([panel.p1.x, panel.p2.x], [panel.p1.y, panel.p2.y], marker = 'o')
        plt.plot([panel.p2.x, panel.p3.x], [panel.p2.y, panel.p3.y], marker = 'o')
        plt.plot([panel.p3.x, panel.p4.x], [panel.p3.y, panel.p4.y], marker = 'o')
        plt.plot([panel.p4.x, panel.p1.x], [panel.p4.y, panel.p1.y], marker = 'o')
    
    # We want it equal
    plt.grid()
    plt.axis('equal')
 */
  type PanelCoords = {x: number[], y: number[]};
  type FullHornPanelCoords = [string, PanelCoords][];
  let all_panels: FullHornPanelCoords = [];
  for (const [key, panel] of panels) {
    let coords: PanelCoords = {x: [], y: []};
    // Push the p1 twice to get p4->p1 line as well
    coords.x.push(panel.p1.x);
    coords.x.push(panel.p2.x);
    coords.x.push(panel.p3.x);
    coords.x.push(panel.p4.x);
    coords.x.push(panel.p1.x);
    coords.y.push(panel.p1.y);
    coords.y.push(panel.p2.y);
    coords.y.push(panel.p3.y);
    coords.y.push(panel.p4.y);
    coords.y.push(panel.p1.y);
    all_panels.push([key, coords]);
  }

  return (
    <Plot
      data={all_panels.map(([key,value]) => {
        return {
          x: value.x,
          y: value.y,
          type: 'scatter',
          mode: 'lines+markers',
          name: key
        }
      })}
        
      
      layout={ {width: 600, height: 840, title: '2D visualization', yaxis: { scaleratio: 1, scaleanchor: 'x' }} }
    />
  );
};