import React from "react";
import NumberInput from "./NumberInput";
import styled from "styled-components"
import { Input, Table, TableCell, TableRow } from "semantic-ui-react";

export default function ParameterInput({parameterName, parameterUnit, value, onChange})
{
    return (
        // <div className="ui right labeled input">
        //     <label for="amount" class="ui label">{ parameterName }</label>
        //     <input type="number" step="0.1" value={value} onChange={(e)=>onChange(e.target.value)}/>
        //     <div className="ui basic label">{ parameterUnit }</div>
        // </div>
        <TableRow>
            <TableCell>{ parameterName }</TableCell>
            <TableCell>
                <Input label={parameterUnit} labelPosition="right" type="number" step="0.1" value={value} onChange={(e)=>onChange(e.target.value)}/>
            </TableCell>
        </TableRow>
    )
}
