class Point {
  readonly x: number;
  readonly y: number;

  constructor(point_x: number, point_y: number) {
    this.x = point_x;
    this.y = point_y;
  }
  
  degToRad(degrees: number): number {
    return degrees * (Math.PI / 180);
  }
  
  get_relative_point(angle: number, length: number): Point {
    /*  Angle is in degrees, clockwise from y+  */
    if (length === null) {
      throw new Error("length is null");
    }

    return new Point(this.x + Math.sin(this.degToRad(angle)) * length, this.y + Math.cos(this.degToRad(angle)) * length);
  }

  get_relative_linesegment(angle: number) : LineSegment {
    /*  Angle is in degrees, clockwise from y+  */
    return new LineSegment(this, this.get_relative_point(angle, 1));
  }

  get_matching_point_horizontal(angle: number, vertical_point: Point) {
    return new Point(this.x + Math.tan(this.degToRad(angle)) * (vertical_point.y - this.y), vertical_point.y);
  }

}

class LineSegment {
  readonly a: Point;
  readonly b: Point;

  constructor(point_a: Point, point_b: Point) {
    this.a = point_a;
    this.b = point_b;
  }

  get_midpoint(): Point {
    var x_avg, y_avg;
    x_avg = (this.a.x + this.b.x) / 2;
    y_avg = (this.a.y + this.b.y) / 2;
    return new Point(x_avg, y_avg);
  }

  get_k_and_offset(): [number, number | null] {
    const numerator = (this.b.y - this.a.y);
    const denumerator = (this.b.x - this.a.x);
    
    if (denumerator === 0 || isNaN(denumerator))
      return [Infinity, null];
    
    const k =  numerator / denumerator;
    const offset = this.a.y - k * this.a.x;

    return [k, offset];
  }

  get_length(): number {
    return Math.sqrt(Math.pow(this.a.x - this.b.x, 2) + Math.pow(this.a.y - this.b.y, 2));
  }

}

class Panel {
  readonly p1: Point;
  readonly p2: Point;
  readonly p3: Point;
  readonly p4: Point;

    constructor(point_1: Point, point_2: Point, point_3: Point, point_4: Point) {
        this.p1 = point_1;
        this.p2 = point_2;
        this.p3 = point_3;
        this.p4 = point_4;
    }
}

export { Point, LineSegment, Panel }