import React, { useState } from "react";
import HornFolderTitle from "./HornFolderTitle";
import ParameterInputs from "./ParameterInputs";
import { TH_Mothorn, Solver, HardParams } from "./th_mothorn";
import type { LengthMap } from "./th_mothorn"
import PanelPlot from "./PanelPlot";
import { Button, Container, Header, Icon, Segment } from "semantic-ui-react";


export default function HornFolder()
{
    let source_list = [
        {name: "Panel thickness", unit: "cm", value: 0.935},
        {name: "Box internal depth", unit: "cm", value: 16.5},
        {name: "S1 size", unit: "cm", value: 50.0},
        {name: "B", unit: "cm", value: 96},
        {name: "C", unit: "cm", value: 48.3},
        {name: "D", unit: "cm", value: 32},
        {name: "E", unit: "cm", value: 70},
        {name: "F", unit: "cm", value: 60},
        {name: "G", unit: "cm", value: 57},
        {name: "H", unit: "cm", value: 19},
        {name: "I", unit: "cm", value: 50},
        {name: "Offset of G in D", unit: "cm", value: 10},
        {name: "Offset of F in H", unit: "cm", value: 5},
        {name: "Offset of Driver in E", unit: "cm", value: 48},
        {name: "Opposite end angle", unit: "deg", value: 0},
        {name: "Exp1 angle", unit: "deg", value: 1},
        {name: "Exp2 angle", unit: "deg", value: 2},
    ];
    
    enum AppState {
        NotOptimized,
        Optimized
    }
    type HornParameterList = {name: string, unit: string, value: number}[];
    const initialState: {appState: AppState, parameterList: HornParameterList, finished_horn: TH_Mothorn} = {
        appState: AppState.NotOptimized,
        parameterList: source_list,
        finished_horn: getHardParamsAndHorn(source_list)[1]
    }

    let [appState, updateAppState] = useState(initialState);


    let handle_change = (name: string, newValue: string)=> {
        const newNumberValue = Number(newValue);
        console.log("Called with " + name + " new value: " + newNumberValue);
        const newList = appState.parameterList.map((item) => {
            if (item.name === name) {
                const updatedItem = {
                  ...item,
                  value: newNumberValue,
                };
      
                return updatedItem;
            }
      
            return item;
        });

        const horn_res = getHardParamsAndHorn(newList);

        updateAppState({
          appState: AppState.NotOptimized,
          finished_horn: horn_res[1],
          parameterList: newList
        });
    }

    function getHardParamsAndHorn(parameterList: HornParameterList): [HardParams, TH_Mothorn] {
  
      const thickness = parameterList.filter((x)=>x.name === "Panel thickness")[0].value;
      const backseat_angle = parameterList.filter((x)=>x.name === "Opposite end angle")[0].value;
      const S1_size_cm = parameterList.filter((x)=>x.name === "S1 size")[0].value;
      const b_length = parameterList.filter((x)=>x.name === "B")[0].value;
      const c_length = parameterList.filter((x)=>x.name === "C")[0].value;
      const horn = getMothorn(parameterList, thickness, backseat_angle, b_length, c_length);
      const depth = parameterList.filter((x)=>x.name === "Box internal depth")[0].value;
      const hards = new HardParams(thickness, depth, backseat_angle, b_length, c_length, S1_size_cm);
      return [hards, horn];
    }
    
    function getMothorn(pars: HornParameterList, thickness: number, backseat_angle: number, b_length: number, c_length: number) {
      const lengths = {
        B: b_length,
        C: c_length,
        D: pars.filter((x)=>x.name === "D")[0].value,
        E: pars.filter((x)=>x.name === "E")[0].value,
        F: pars.filter((x)=>x.name === "F")[0].value,
        G: pars.filter((x)=>x.name === "G")[0].value,
        H: pars.filter((x)=>x.name === "H")[0].value,
        I: pars.filter((x)=>x.name === "I")[0].value,
      };
      const offsets = {
        G_in_D: pars.filter((x)=>x.name === "Offset of G in D")[0].value,
        F_in_H: pars.filter((x)=>x.name === "Offset of F in H")[0].value,
        Driv_in_E: pars.filter((x)=>x.name === "Offset of Driver in E")[0].value
      };
      const exp1_angle = pars.filter((x)=>x.name === "Exp1 angle")[0].value;
      const exp2_angle = pars.filter((x)=>x.name === "Exp2 angle")[0].value;
    
      const horn = new TH_Mothorn(lengths, thickness, backseat_angle, exp1_angle, exp2_angle, offsets);
      return horn;
    }

    function fromMothorn(horn: TH_Mothorn) {
      function replace_in_list_from_indexable(key: keyof LengthMap) {
        return {...appState.parameterList.filter((x)=>x.name === key)[0], value: horn.lengths[key]}
      }
      
      const res: HornParameterList = [
        appState.parameterList.filter((x)=>x.name === "Panel thickness")[0],
        appState.parameterList.filter((x)=>x.name === "Box internal depth")[0],
        appState.parameterList.filter((x)=>x.name === "S1 size")[0],
        replace_in_list_from_indexable("B"),
        replace_in_list_from_indexable("C"),
        replace_in_list_from_indexable("D"),
        replace_in_list_from_indexable("E"),
        replace_in_list_from_indexable("F"),
        replace_in_list_from_indexable("G"),
        replace_in_list_from_indexable("H"),
        replace_in_list_from_indexable("I"),
        {...appState.parameterList.filter((x)=>x.name === "Offset of F in H")[0], value: horn.offsets["F_in_H"]},
        {...appState.parameterList.filter((x)=>x.name === "Offset of G in D")[0], value: horn.offsets["G_in_D"]},
        {...appState.parameterList.filter((x)=>x.name === "Offset of Driver in E")[0], value: horn.offsets["Driv_in_E"]},
        appState.parameterList.filter((x)=>x.name === "Opposite end angle")[0],
        {...appState.parameterList.filter((x)=>x.name === "Exp1 angle")[0], value: horn.exp1_angle},
        {...appState.parameterList.filter((x)=>x.name === "Exp2 angle")[0], value: horn.exp2_angle},
      ];
      const rounded = res.map((vald)=>{return {...vald, value: Number(vald.value.toFixed(2))};});

      return rounded;
    }

    async function runSolver() {
        console.log("Starting to optimize design error");

        const [hards, horn] = getHardParamsAndHorn(appState.parameterList);
        const solver = new Solver(hards, horn);
        console.log(solver.result_horn);

        updateAppState({
          appState: AppState.Optimized,
          finished_horn: solver.result_horn,
          parameterList: fromMothorn(solver.result_horn)
        });
    };

    function getButton(optimized: boolean)
    {
      if (optimized)
      {
        return (
        <Button icon basic labelPosition='right' color='green' onClick={runSolver}>
          <Icon name='checkmark'/>
          Optimized!
        </Button>
        )
      }
      else
      {
        return (
        <Button icon primary labelPosition='right' onClick={runSolver}>
          <Icon name='heart'/>
          Optimize
        </Button>
        )
      }
    }

    return (
        <Container>
          <Segment.Group>
            <Segment>
              <HornFolderTitle title={"Horn Folder"}/>
            </Segment>
            <Segment>
              <Segment.Group horizontal>
                <Segment.Group>
                  <Segment>
                    <Header as='h2'>
                      Parameters
                    </Header>
                  </Segment>
                  <Segment>
                    <ParameterInputs inputs={appState.parameterList} inputUpdated={handle_change}/>
                  </Segment>
                  <Segment>
                    {getButton(appState.appState === AppState.Optimized)}
                  </Segment>
                </Segment.Group>
                <Segment>
                  <PanelPlot panels={appState.finished_horn.panels}/>
                </Segment>
              </Segment.Group>
            </Segment>
          </Segment.Group>
        </Container>
    );
}

