import { Panel, LineSegment, Point } from './horn_model_definitions'
const fmin = require('fmin')

type LengthMap = {
  B: number,
  C: number,
  D: number,
  E: number,
  F: number,
  G: number,
  H: number,
  I: number,
}
type OffsetMap = {
  G_in_D: number,
  F_in_H: number,
  Driv_in_E: number,
}

class TH_Mothorn {
    /*lengths is a dict of 7 lengths
    [B, C, D, E, G, H, I]
     Angle is the angle of the part
    resting to back seat
     Offsets are the offsets of some panels
    [Offset for panel F in panel A]
     Variables that can be modified here are
    all lengths not including B, C, A and
    all offsets.
    */

    lengths: LengthMap;
    offsets: OffsetMap;
    panels: Map<string, Panel>;
    thickness: number;
    back_seat_angle: number;
    exp1_angle: number;
    exp2_angle: number;
    H_angle: number;
    I_angle: number;
    G_angle: number;
    E_angle: number;
    F_angle: number;

    constructor(lengths: LengthMap, thickness: number, back_seat_angle: number, exp1_angle: number, exp2_angle: number, offsets: OffsetMap) {
        this.lengths = lengths;
        this.thickness = thickness;
        this.back_seat_angle = back_seat_angle;
        this.exp1_angle = exp1_angle;
        this.exp2_angle = exp2_angle;
        this.H_angle = back_seat_angle - exp2_angle;
        this.I_angle = 90 - exp2_angle;
        this.G_angle = this.I_angle + exp1_angle;
        this.E_angle = this.G_angle - exp1_angle;
        this.F_angle = this.E_angle;
        this.offsets = offsets;
        this.panels = this.generate_panels(thickness, lengths, offsets, back_seat_angle);
    }

    generate_generic_panel(p1: Point, x: number, y: number): Panel {
        const p2 = p1.get_relative_point(90, x);
        const p3 = p2.get_relative_point(180, y);
        const p4 = p3.get_relative_point(270, x);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_B(p1: Point, thickness: number, length: number): Panel {
        return this.generate_generic_panel(p1, length, thickness);
    }

    generate_panel_C(p1: Point, thickness: number, length: number, angle: number): Panel {
        const p2 = p1.get_relative_point(angle + 90, thickness);
        const p3 = p2.get_relative_point(angle + 180, length);
        const p4 = p3.get_relative_point(angle + 270, thickness);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_A(p2: Point, thickness: number): Panel {
        const p3 = p2.get_relative_point(180, thickness);
        const p4 = new Point(0, p3.y);
        const p1 = p4.get_relative_point(0, thickness);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_F(p3: Point, thickness: number, length: number, angle: number): Panel {
        const p4 = p3.get_relative_point(angle + 180, length);
        const p1 = p4.get_relative_point(angle + 270, thickness);
        const p2 = p1.get_relative_point(angle, length);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_I(p3: Point, thickness: number, length: number, angle: number): Panel {
        const p4 = p3.get_relative_point(angle + 180, length);
        const p1 = p4.get_relative_point(angle + 270, thickness);
        const p2 = p1.get_relative_point(angle, length);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_E(p1: Point, thickness: number, length: number, angle: number): Panel {
        const p2 = p1.get_relative_point(angle, length);
        const p3 = p2.get_relative_point(angle + 90, thickness);
        const p4 = p3.get_relative_point(angle + 180, length);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_D(p4: Point, thickness: number, length: number): Panel {
        const p1 = p4.get_relative_point(0, length);
        const p2 = p1.get_relative_point(90, thickness);
        const p3 = p2.get_relative_point(180, length);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_G(p1: Point, thickness: number, length: number, angle: number): Panel {
        const p2 = p1.get_relative_point(angle, length);
        const p3 = p2.get_relative_point(angle + 90, thickness);
        const p4 = p3.get_relative_point(angle + 180, length);
        return new Panel(p1, p2, p3, p4);
    }

    generate_panel_H(p2: Point, thickness: number, length: number, angle: number): Panel {
        const p3 = p2.get_relative_point(angle, length);
        const p4 = p3.get_relative_point(angle + 90, thickness);
        const p1 = p4.get_relative_point(angle + 180, length);
        return new Panel(p1, p2, p3, p4);
    }

    deg_to_rad(degrees: number): number {
      return degrees * (Math.PI/180);
    }

    generate_panels(thickness: number, lengths: LengthMap, offsets: OffsetMap, back_seat_angle: number) {
        const panels = new Map<string, Panel>();
        panels.set("B", this.generate_panel_B(new Point(0, 0), thickness, lengths["B"]));
        const offset_from_B_end = Math.cos(this.deg_to_rad(back_seat_angle)) * thickness;
        panels.set("C", this.generate_panel_C(panels.get("B")!.p3.get_relative_point(270, offset_from_B_end), thickness, lengths["C"], back_seat_angle));
        panels.set("A", this.generate_panel_A(new Point(panels.get("C")!.p3.x, panels.get("C")!.p4.y), thickness));
        panels.set("D", this.generate_panel_D(panels.get("A")!.p1, thickness, lengths["D"]));
        panels.set("G", this.generate_panel_G(panels.get("D")!.p2.get_relative_point(180, offsets["G_in_D"]), thickness, lengths["G"], this.G_angle));
        panels.set("E", this.generate_panel_E(panels.get("D")!.p2, thickness, lengths["E"], this.E_angle));
        const l1 = Math.tan(this.deg_to_rad(back_seat_angle)) * thickness;
        const l2 = thickness / Math.cos(this.deg_to_rad(back_seat_angle));
        panels.set("H", this.generate_panel_H(panels.get("E")!.p2.get_relative_point(this.H_angle - 180, l1 + l2), thickness, lengths["H"], this.H_angle - 180));
        panels.set("F", this.generate_panel_F(panels.get("H")!.p1.get_relative_point(this.H_angle - 180, offsets["F_in_H"]), thickness, lengths["F"], this.F_angle));
        const helper_length = Math.cos(this.deg_to_rad(-back_seat_angle)) * thickness;
        panels.set("I", this.generate_panel_I(panels.get("H")!.p3.get_relative_point(this.H_angle - 180, helper_length), thickness, lengths["I"], this.I_angle));
        return panels;
    }

}

class Path {
    angle_hori_E_and_F: number;
    angle_hori_F_and_G: number;
    angle_hori_G_and_I: number;
    angle_hori_I_and_A: number;
    angle_hori_B_and_E: number;
    angle_vertical_D_and_I: number;
    angle_vertical_D_and_F: number;
    angle_vertical_G_and_H: number;
    angle_vertical_H_and_C: number;
    path_segments!: LineSegment[];
    areas: [number, number][];

    constructor(horn: TH_Mothorn, depth: number) {
      this.angle_hori_E_and_F = horn.F_angle;
      this.angle_hori_F_and_G = this.halfway_angle(horn.G_angle, horn.F_angle);
      this.angle_hori_G_and_I = this.halfway_angle(horn.G_angle, horn.I_angle);
      this.angle_hori_I_and_A = this.halfway_angle(90, horn.I_angle);
      this.angle_hori_B_and_E = this.halfway_angle(90, horn.E_angle);
      this.angle_vertical_D_and_I = this.halfway_angle(0, horn.I_angle - 90);
      this.angle_vertical_D_and_F = this.halfway_angle(0, horn.F_angle - 90);
      this.angle_vertical_G_and_H = this.halfway_angle(horn.G_angle - 90, horn.H_angle);
      this.angle_vertical_H_and_C = this.halfway_angle(horn.back_seat_angle, horn.H_angle);
      this.generate_path(horn);
      this.areas = [];
      this.calculate_areas(depth);
    }

    get_S1() {
        return this.areas[0][1];
    }

    get_S2() {
        return this.areas[1][1];
    }

    get_S3() {
        return this.areas[34][1];
    }

    get_point_before_S3() {
        return this.areas[33][1];
    }

    get_S4() {
        return this.areas.slice(-1)[0][1];
    }

    get_L12() {
        return this.areas[1][0];
    }

    get_L23() {
        var s2, s3;
        s2 = this.areas[1];
        s3 = this.areas[34];
        return s3[0] - s2[0];
    }

    get_L34() {
        var s3, s4;
        s3 = this.areas[34];
        s4 = this.areas.slice(-1)[0];
        return s4[0] - s3[0];
    }

    get_L23_minus_last_section() {
        var s2, s3m;
        s2 = this.areas[1];
        s3m = this.areas[33];
        return s3m[0] - s2[0];
    }

    generate_path(rock: TH_Mothorn) {
        this.path_segments = new Array(36);
        this.generate_up_to_first_180(rock);
        this.generate_second_180(rock);
        this.generate_third_180(rock);
        this.calculate_last_corners(rock);
        this.generate_S3_and_throat(rock);
    }

    generate_up_to_first_180(g: TH_Mothorn) {
        var driver_opposite, driver_point, outside_p1, outside_p2, outside_p3, outside_p4, pan, s1_opposite;
        pan = g.panels;
        s1_opposite = this.get_intersection(pan.get("F")!.p2.get_relative_linesegment(this.angle_hori_E_and_F - 90), new LineSegment(pan.get("E")!.p4, pan.get("E")!.p3));
        this.path_segments[0] = new LineSegment(pan.get("F")!.p2, s1_opposite);
        driver_point = pan.get("E")!.p4.get_relative_point(g.E_angle, g.offsets["Driv_in_E"]);
        driver_opposite = this.get_intersection(driver_point.get_relative_linesegment(this.angle_hori_E_and_F - 90), new LineSegment(pan.get("F")!.p1, pan.get("F")!.p2));
        this.path_segments[1] = new LineSegment(driver_point, driver_opposite);
        outside_p1 = this.get_intersection(pan.get("F")!.p1.get_relative_linesegment(this.angle_hori_E_and_F - 90), new LineSegment(pan.get("E")!.p4, pan.get("E")!.p3));
        outside_p2 = this.get_intersection(pan.get("F")!.p1.get_relative_linesegment(this.angle_vertical_D_and_F - 90), new LineSegment(pan.get("D")!.p2, pan.get("D")!.p3));
        outside_p3 = this.get_intersection(pan.get("F")!.p4.get_relative_linesegment(this.angle_vertical_D_and_F - 90), new LineSegment(pan.get("D")!.p2, pan.get("D")!.p3));
        outside_p4 = this.get_intersection(pan.get("F")!.p4.get_relative_linesegment(this.angle_hori_F_and_G + 90), new LineSegment(pan.get("G")!.p1, pan.get("G")!.p2));
        this.path_segments[2] = new LineSegment(pan.get("F")!.p1, outside_p1);
        this.path_segments[3] = new LineSegment(pan.get("F")!.p1, this.halfway(pan.get("E")!.p4, outside_p1));
        this.path_segments[4] = new LineSegment(pan.get("F")!.p1, this.halfway(pan.get("E")!.p4, outside_p2));
        this.path_segments[5] = new LineSegment(pan.get("F")!.p1, outside_p2);
        this.path_segments[6] = new LineSegment(pan.get("F")!.p4, outside_p3);
        this.path_segments[7] = new LineSegment(pan.get("F")!.p4, this.halfway(pan.get("G")!.p1, outside_p3));
        this.path_segments[8] = new LineSegment(pan.get("F")!.p4, this.halfway(pan.get("G")!.p1, outside_p4));
        this.path_segments[9] = new LineSegment(pan.get("F")!.p4, outside_p4);
    }

    generate_second_180(g: TH_Mothorn) {
        var imaginary_corner, outside_p1, outside_p2, outside_p3, outside_p4, pan;
        pan = g.panels;
        outside_p1 = this.get_intersection(pan.get("G")!.p2.get_relative_linesegment(this.angle_hori_F_and_G - 90), new LineSegment(pan.get("F")!.p4, pan.get("F")!.p3));
        imaginary_corner = this.get_intersection(pan.get("H")!.p1.get_relative_linesegment(g.H_angle - 180), new LineSegment(pan.get("F")!.p4, pan.get("F")!.p3));
        outside_p2 = this.get_intersection(pan.get("G")!.p2.get_relative_linesegment(this.angle_vertical_G_and_H + 90), new LineSegment(pan.get("H")!.p1, pan.get("H")!.p4));
        outside_p3 = this.get_intersection(pan.get("G")!.p3.get_relative_linesegment(this.angle_vertical_G_and_H + 90), new LineSegment(pan.get("H")!.p1, pan.get("H")!.p4));
        outside_p4 = this.get_intersection(pan.get("G")!.p3.get_relative_linesegment(this.angle_hori_G_and_I + 90), new LineSegment(pan.get("I")!.p1, pan.get("I")!.p2));
        this.path_segments[10] = new LineSegment(pan.get("G")!.p2, outside_p1);
        this.path_segments[11] = new LineSegment(pan.get("G")!.p2, this.halfway(outside_p1, imaginary_corner));
        this.path_segments[12] = new LineSegment(pan.get("G")!.p2, this.halfway(outside_p2, imaginary_corner));
        this.path_segments[13] = new LineSegment(pan.get("G")!.p2, outside_p2);
        this.path_segments[14] = new LineSegment(pan.get("G")!.p3, outside_p3);
        this.path_segments[15] = new LineSegment(pan.get("G")!.p3, this.halfway(outside_p3, pan.get("H")!.p4));
        this.path_segments[16] = new LineSegment(pan.get("G")!.p3, this.halfway(outside_p4, pan.get("H")!.p4));
        this.path_segments[17] = new LineSegment(pan.get("G")!.p3, outside_p4);
    }

    generate_third_180(g: TH_Mothorn) {
        var outside_p1, outside_p2, outside_p3, outside_p4, pan;
        pan = g.panels;
        outside_p1 = this.get_intersection(pan.get("I")!.p1.get_relative_linesegment(this.angle_hori_G_and_I - 90), new LineSegment(pan.get("G")!.p4, pan.get("G")!.p3));
        outside_p2 = this.get_intersection(pan.get("I")!.p1.get_relative_linesegment(this.angle_vertical_D_and_I - 90), new LineSegment(pan.get("D")!.p2, pan.get("D")!.p3));
        outside_p3 = this.get_intersection(pan.get("I")!.p4.get_relative_linesegment(this.angle_vertical_D_and_I - 90), new LineSegment(pan.get("D")!.p2, pan.get("D")!.p3));
        outside_p4 = this.get_intersection(pan.get("I")!.p4.get_relative_linesegment(this.angle_hori_I_and_A + 90), new LineSegment(pan.get("A")!.p1, pan.get("A")!.p2));
        this.path_segments[18] = new LineSegment(pan.get("I")!.p1, outside_p1);
        this.path_segments[19] = new LineSegment(pan.get("I")!.p1, this.halfway(pan.get("G")!.p4, outside_p1));
        this.path_segments[20] = new LineSegment(pan.get("I")!.p1, this.halfway(pan.get("G")!.p4, outside_p2));
        this.path_segments[21] = new LineSegment(pan.get("I")!.p1, outside_p2);
        this.path_segments[22] = new LineSegment(pan.get("I")!.p4, outside_p3);
        this.path_segments[23] = new LineSegment(pan.get("I")!.p4, this.halfway(pan.get("D")!.p3, outside_p3));
        this.path_segments[24] = new LineSegment(pan.get("I")!.p4, this.halfway(pan.get("D")!.p3, outside_p4));
        this.path_segments[25] = new LineSegment(pan.get("I")!.p4, outside_p4);
    }

    calculate_last_corners(g: TH_Mothorn) {
        var imaginary_corner, outside_p1, outside_p2, outside_p3, outside_p4, pan;
        pan = g.panels;
        outside_p1 = this.get_intersection(pan.get("I")!.p3.get_relative_linesegment(this.angle_hori_I_and_A + 90), new LineSegment(pan.get("A")!.p1, pan.get("A")!.p2));
        outside_p2 = this.get_intersection(pan.get("I")!.p3.get_relative_linesegment(this.angle_vertical_H_and_C + 90), new LineSegment(pan.get("C")!.p1, pan.get("C")!.p4));
        outside_p3 = this.get_intersection(pan.get("E")!.p2.get_relative_linesegment(this.angle_vertical_H_and_C + 90), new LineSegment(pan.get("C")!.p1, pan.get("C")!.p4));
        outside_p4 = this.get_intersection(pan.get("E")!.p2.get_relative_linesegment(this.angle_hori_B_and_E - 90), new LineSegment(pan.get("B")!.p4, pan.get("B")!.p3));
        imaginary_corner = this.get_intersection(new LineSegment(pan.get("B")!.p4, pan.get("B")!.p3), new LineSegment(pan.get("C")!.p4, pan.get("C")!.p1));
        this.path_segments[26] = new LineSegment(pan.get("I")!.p3, outside_p1);
        this.path_segments[27] = new LineSegment(pan.get("I")!.p3, this.halfway(outside_p1, pan.get("C")!.p4));
        this.path_segments[28] = new LineSegment(pan.get("I")!.p3, this.halfway(outside_p2, pan.get("C")!.p4));
        this.path_segments[29] = new LineSegment(pan.get("I")!.p3, outside_p2);
        this.path_segments[30] = new LineSegment(pan.get("E")!.p2, outside_p3);
        this.path_segments[31] = new LineSegment(pan.get("E")!.p2, this.halfway(outside_p3, imaginary_corner));
        this.path_segments[32] = new LineSegment(pan.get("E")!.p2, this.halfway(outside_p4, imaginary_corner));
        this.path_segments[33] = new LineSegment(pan.get("E")!.p2, outside_p4);
    }

    generate_S3_and_throat(g: TH_Mothorn) {
        var S3_point, driver_opposite, outside_p1, pan;
        pan = g.panels;
        S3_point = pan.get("E")!.p1.get_relative_point(g.E_angle, g.offsets["Driv_in_E"]);
        driver_opposite = this.get_intersection(S3_point.get_relative_linesegment(this.angle_hori_B_and_E - 90), new LineSegment(pan.get("B")!.p4, pan.get("B")!.p3));
        this.path_segments[34] = new LineSegment(S3_point, driver_opposite);
        outside_p1 = this.get_intersection(pan.get("B")!.p4.get_relative_linesegment(this.angle_hori_B_and_E + 90), new LineSegment(pan.get("E")!.p1, pan.get("E")!.p2));
        this.path_segments[35] = new LineSegment(pan.get("B")!.p4, outside_p1);
    }

    calculate_areas(depth: number) {
        let at_length = 0;
        let previous = this.path_segments[0].get_midpoint();

        for (var seg, _pj_c = 0, _pj_a = this.path_segments, _pj_b = _pj_a.length; _pj_c < _pj_b; _pj_c += 1) {
            seg = _pj_a[_pj_c];
            at_length += new LineSegment(previous, seg.get_midpoint()).get_length();
            this.areas.push([at_length, seg.get_length() * depth]);
            previous = seg.get_midpoint();
        }
    }

    get_total_length() {
        return this.areas.slice(-1)[0][0];
    }

    halfway(p1: Point, p2: Point) {
        var ls;
        ls = new LineSegment(p1, p2);
        return ls.get_midpoint();
    }

    halfway_angle(ang1: number, ang2: number) {
        return (ang1 % 360 + ang2 % 360) / 2;
    }

    get_intersection(ls1: LineSegment, ls2: LineSegment) {
        var k1, k2, offset1, offset2, x, y;
        [k1, offset1] = ls1.get_k_and_offset();
        [k2, offset2] = ls2.get_k_and_offset();

        if (k1 === Infinity) {
            x = ls1.a.x;
            y = k2 * x + offset2!;
        } else {
            if (k2 === Infinity) {
                x = ls2.a.x;
                y = k1 * x + offset1!;
            } else {
                x = (offset1! - offset2!) / (k2 - k1);
                y = k1 * x + offset1!;
            }
        }

        return new Point(x, y);
    }

}

class HardParams {
  panel_thickness: number;
  depth: number;
  bs_angle: number;
  B_length: number;
  C_length: number;
  S1: number;
  result_horn: null;

    constructor(thickness: number, int_depth: number, back_seat_angle: number, B_length: number, C_length: number, S1: number) {
        this.panel_thickness = thickness;
        this.depth = int_depth;
        this.bs_angle = back_seat_angle;
        this.B_length = B_length;
        this.C_length = C_length;
        this.S1 = S1;
        this.result_horn = null;
    }
}

function calculate_A_at(start_area: number, m: number, l_from_start: number): number {
  return start_area * Math.exp(m * l_from_start);
}

function expected_area_at(start: [number, number], end: [number, number], l_from_start: number): number {
  return (end[1] - start[1]) * l_from_start / (end[0] - start[0]) + start[1];
}

function calculate_exponential_m(S1: number, S4: number, length: number): number {
  return (Math.log(S4) - Math.log(S1)) / length;
}

function linspace(start: number, stop: number, num: number, endpoint = true) {
  const div = endpoint ? (num - 1) : num;
  const step = (stop - start) / div;
  return Array.from({length: num}, (_, i) => start + step * i);
}

class Solver {
  hard_params: HardParams;
  result_horn!: TH_Mothorn;
  result_path!: Path;
  constructor(hard_params: HardParams, initial_horn: TH_Mothorn) {
    /*
    Initial horn consists of guesses for all values
    that haven't been set as hard params
    */
    this.hard_params = hard_params;
    this.solve(initial_horn, hard_params);
  }



  solve(current_horn: TH_Mothorn, hard_params: HardParams) {
    console.log("Solving with current horn:" + JSON.stringify(current_horn, null, 4) + ", hard params:" + JSON.stringify(hard_params, null, 4));
    type HornCostFunction = (x0: number[], hard_params: HardParams, current_horn: TH_Mothorn)=>number;
    const mini = (
      funn: HornCostFunction,
      vx0: number[],
      current_horn: TH_Mothorn,
      hard_params: HardParams) => {
        return fmin.nelderMead(
        (x0: number[])=>funn(x0, hard_params, current_horn),
        vx0);
    };

    const minimize_length_err = (optimize_todo: [keyof LengthMap, HornCostFunction][], current_horn: TH_Mothorn, hard_params: HardParams) => {
      var target_dict : LengthMap;

      optimize_todo.forEach(element => {
        const [key, cost_fun] = element;
        target_dict = current_horn.lengths;
        // We are optimizing only one value of the horn
        const res = mini(cost_fun, [ target_dict[key] ], current_horn, hard_params);

        target_dict[key] = res.x[0];
        current_horn = new TH_Mothorn(current_horn.lengths,
          current_horn.thickness,
          current_horn.back_seat_angle,
          current_horn.exp1_angle,
          current_horn.exp2_angle,
          current_horn.offsets);
      });
      return current_horn;
    };

    const minimize_offset_err = (optimize_todo: [keyof OffsetMap, HornCostFunction][], current_horn: TH_Mothorn, hard_params: HardParams) => {
      var target_dict : OffsetMap;

      optimize_todo.forEach(element => {
        const [key, cost_fun] = element;
        target_dict = current_horn.offsets;
        // We are optimizing only one value of the horn
        const res = mini(cost_fun, [ target_dict[key] ], current_horn, hard_params);

        target_dict[key] = res.x[0];
        current_horn = new TH_Mothorn(current_horn.lengths,
          current_horn.thickness,
          current_horn.back_seat_angle,
          current_horn.exp1_angle,
          current_horn.exp2_angle,
          current_horn.offsets);
      });
      return current_horn;
    };
    

    function offset_to_zero(arr: number[]) {
      let count = 0;
      let total = 0;
      for (let val of arr) {
          total += val;
          count++;
      }
      const mean = total / count;

      const ret: number[] = [];
      for (let val of arr) {
        ret.push(val - mean);
      }
      return ret;
    };

    const calculate_array_SSE = (arr1: number[], arr2: number[]) => {
      var error;
      error = 0;

      for (var i = 0, _pj_a = arr1.length; i < _pj_a; i += 1) {
        error += Math.pow(arr1[i] - arr2[i], 2);
      }

      return error;
    };

    const generic_exp_to_par_error = (exp_start_point: [number, number], m: number, area_1: [number, number], area_2: [number, number], zero_offset = true) => {
      const start_len = area_1[0];
      const end_len = area_2[0];
      const to_test = linspace(start_len, end_len, 50);
      let actuals: number[] = [];
      let expected: number[] = [];

      for (var point, _pj_c = 0, _pj_a = to_test, _pj_b = _pj_a.length; _pj_c < _pj_b; _pj_c += 1) {
        point = _pj_a[_pj_c];
        actuals.push(expected_area_at(area_1, area_2, point - area_1[0]));
        expected.push(calculate_A_at(exp_start_point[1], m, point - exp_start_point[0]));
      }

      if (zero_offset) {
        actuals = offset_to_zero(actuals);
        expected = offset_to_zero(expected);
      }

      return calculate_array_SSE(actuals, expected);
    };

    function exp1_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      var G_I_error, H_G_error, horn, m, path;
      horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, x[0], current_horn.exp2_angle, current_horn.offsets);
      path = new Path(horn, hard_params.depth);
      m = calculate_exponential_m(path.get_S2(), path.get_point_before_S3(), path.get_L23_minus_last_section());
      H_G_error = generic_exp_to_par_error(path.areas[1], m, path.areas[9], path.areas[10]);
      G_I_error = generic_exp_to_par_error(path.areas[1], m, path.areas[17], path.areas[18]);
      return H_G_error + G_I_error;
    };

    function exp2_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      var H_C_error, I_A_error, horn, m, path;
      horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, x[0], current_horn.offsets);
      path = new Path(horn, hard_params.depth);
      m = calculate_exponential_m(path.get_S2(), path.get_point_before_S3(), path.get_L23_minus_last_section());
      I_A_error = generic_exp_to_par_error(path.areas[1], m, path.areas[25], path.areas[26]);
      H_C_error = generic_exp_to_par_error(path.areas[1], m, path.areas[29], path.areas[30]);
      return I_A_error + 0.8 * H_C_error;
    };

    function horn_generic_length_error_exp(x: number[], index: (keyof LengthMap), hard_params: HardParams, current_horn: TH_Mothorn, start_idx: number, end_idx: number | null = null): number {
      var expected, horn, m, path, segment;
      let lengths = { ...current_horn.lengths} ;
      lengths[index] = x[0];
      horn = new TH_Mothorn(lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, current_horn.exp2_angle, current_horn.offsets);
      path = new Path(horn, hard_params.depth);
      m = calculate_exponential_m(path.get_S2(), path.get_point_before_S3(), path.get_L23_minus_last_section());

      if (end_idx === null) {
        segment = path.areas[start_idx];
        expected = calculate_A_at(path.get_S2(), m, segment[0] - path.get_L12());
        return Math.abs(segment[1] - expected);
      } else {
        return generic_exp_to_par_error(path.areas[1], m, path.areas[start_idx], path.areas[end_idx], false);
      }
    };

    function horn_F_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      return horn_generic_length_error_exp(x, "F", hard_params, current_horn, 6);
    };

    function horn_G_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      return horn_generic_length_error_exp(x, "G", hard_params, current_horn, 14);
    };

    function horn_H_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      return horn_generic_length_error_exp(x, "H", hard_params, current_horn, 17, 18);
    };

    function horn_G_in_D_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      var horn, m, path;
      let offsets = { ...current_horn.offsets} ;
      offsets["G_in_D"] = x[0];
      horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, current_horn.exp2_angle, offsets);
      path = new Path(horn, hard_params.depth);
      m = calculate_exponential_m(path.get_S2(), path.get_point_before_S3(), path.get_L23_minus_last_section());
      return generic_exp_to_par_error(path.areas[1], m, path.areas[9], path.areas[10], false);
    };

    function horn_I_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      return horn_generic_length_error_exp(x, "I", hard_params, current_horn, 22);
    };

    function horn_D_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      return horn_generic_length_error_exp(x, "D", hard_params, current_horn, 25, 26);
    };

    function horn_E_len_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      /*  x is an offset to apply to two different values  */
      var horn, m, path;
      const lengths =  {...current_horn.lengths };
      lengths["E"] += x[0];
      lengths["F"] += x[0];
      lengths["G"] += x[0];
      lengths["I"] += x[0];
      horn = new TH_Mothorn(lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, current_horn.exp2_angle, current_horn.offsets);
      path = new Path(horn, hard_params.depth);
      m = calculate_exponential_m(path.get_S2(), path.get_point_before_S3(), path.get_L23_minus_last_section());
      return generic_exp_to_par_error(path.areas[1], m, path.areas[29], path.areas[30], false);
    };

    function horn_F_in_H_error(x: number[], hard_params: HardParams, current_horn: TH_Mothorn) {
      var expected_area, horn, path, segment;
      let offsets = { ...current_horn.offsets} ;
      offsets["F_in_H"] = x[0];
      horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, current_horn.exp2_angle, offsets);
      path = new Path(horn, hard_params.depth);
      segment = path.areas[0];
      expected_area = hard_params.S1;
      return Math.abs(segment[1] - expected_area);
    };

    for (var i = 0; i < 10; i++) {
      // Optimize F_in_H
      current_horn = minimize_offset_err(
        [["F_in_H", horn_F_in_H_error]], current_horn, hard_params);

      // Optimize exp1 angle
      const res = mini(exp1_error, [current_horn.exp1_angle], current_horn, hard_params);
      const new_exp1 = res.x[0];
      current_horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, new_exp1, current_horn.exp2_angle, current_horn.offsets);

      // Optimize exp2 angle
      const res2 = mini(exp2_error, [current_horn.exp2_angle], current_horn, hard_params);
      const new_exp2 = res2.x[0];
      current_horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, new_exp2, current_horn.offsets);
      
      // Optimize rest
      current_horn = minimize_length_err(
        [["F", horn_F_len_error]], current_horn, hard_params);
      current_horn = minimize_offset_err(
        [["G_in_D", horn_G_in_D_error],], current_horn, hard_params);
      current_horn = minimize_length_err(
        [["G", horn_G_len_error],
        ["H", horn_H_len_error],
        ["I", horn_I_len_error],
        ["D", horn_D_len_error]], current_horn, hard_params);

      // Optimize E length
      const res3 = mini(horn_E_len_error, [0], current_horn, hard_params);
      current_horn.lengths["E"] += res3.x[0];
      current_horn.lengths["F"] += res3.x[0];
      current_horn.lengths["G"] += res3.x[0];
      current_horn.lengths["I"] += res3.x[0];
      current_horn = new TH_Mothorn(current_horn.lengths, current_horn.thickness, current_horn.back_seat_angle, current_horn.exp1_angle, current_horn.exp2_angle, current_horn.offsets);
      
      console.log(`Finished loop ${i}`);
    }

    this.result_horn = current_horn;
    this.result_path = new Path(current_horn, hard_params.depth);
  }

}

function Visualizer(path: Path) {
  const at_len_real: number[] = [];
  const area_real: number[] = [];

  for (const x of path.areas) {
    area_real.push(x[1]);
    at_len_real.push(x[0]);
  }

  const at_len_ideal = [0, path.get_L12()];
  const area_ideal = [path.get_S1(), path.get_S2()];

  const m = calculate_exponential_m(
    path.get_S2(),
    path.get_point_before_S3(),
    path.get_L23_minus_last_section());

  const exp_points = linspace(0, path.get_L23_minus_last_section(), 100);
  for (const p of exp_points) {
    at_len_ideal.push(path.get_L12() + p)
    area_ideal.push(calculate_A_at(path.get_S2(), m, p));
  }

  at_len_ideal.push(path.get_total_length());
  area_ideal.push(path.get_S4());

  return {
    at_len_real: at_len_real,
    area_real: area_real,
    area_ideal: area_ideal,
    at_len_ideal: at_len_ideal
  };
}

export { TH_Mothorn, Path, HardParams, Solver, Visualizer }
export type { LengthMap, OffsetMap }
